/* Webfont: KdG-Regular */
@font-face {
  font-family: 'KdG';
  src: url("../fonts/KdG-Regular.eot");
  /* IE9 Compat Modes */
  src: url("../fonts/KdG-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/KdG-Regular.woff") format("woff"), url("../fonts/KdG-Regular.ttf") format("truetype"), url("../fonts/KdG-Regular.svg#KdG-Regular") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

html {
  box-sizing: border-box;
}

ul:not(.browser-default) {
  padding-left: 0;
  list-style-type: none;
}

ul:not(.browser-default) li {
  list-style-type: none;
}

a {
