.wrapper_login .login {
  border-radius: 5px;
  background-color: #1d1d1b;
  box-shadow: 0px 27px 24px 0px rgba(0, 0, 0, 0.2), 0px 40px 70px 0px rgba(0, 0, 0, 0.22);
  width: 60%;
  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper_login .login form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrapper_login .login form .input-field label, .wrapper_login .login form .input-field input {
  font-family: "KdG";
  font-weight: 100;
}

.wrapper_login .login form .input-field label {
  font-size: 16px !important;
}

.wrapper_login .login form .input-field input {
  width: 300px;
  color: white;
  font-family: "Roboto", serif;
}

.wrapper_login .login form span {
  color: #DA0813 !important;
}

.wrapper_login .login form .button_bevestig {
  margin-top: 20px;
}

content {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 20px;
  box-sizing: border-box;
  padding-top: 50px;
  width: 80%;
  height: 85%;
}

content header {
  width: 100%;
  min-height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: row wrap;
}

content .info {
  width: 100%;
  min-height: 35px;
  display: flex;
  justify-content: space-between;
}

content .info ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 40%;
  transition: width 1s;
}

@media screen and (max-width: 1450px) {
  content .info ul {
    width: 50%;
    transition: width 1s;
  }
}

@media screen and (max-width: 1200px) {
  content .info ul {
    width: 100%;
    transition: width 1s;
  }
}

content .info ul li {
  font-family: "Roboto", serif;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

@media screen and (max-width: 600px) {
  content .info ul li {
    flex-direction: column;
    align-items: flex-start;
  }
}

content .info ul li .label {
  display: inline-block;
  font-size: 16px;
  color: #1d1d1b;
  width: 20%;
}

content .info ul li .data {
  font-size: 24px;
}

content .category {
  width: 100%;
  margin-top: 5%;
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
}

content .category h2 {
  width: 100%;
}

content .category h3 {
  font-family: 'KdG';
  font-size: 30px;
  width: 100%;
  margin: 0;
}

content .category .card_container {
  margin-top: 60px;
}

content .choice {
  width: 100%;
  margin-top: 5%;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
}

content .choice .card_container {
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
  margin-top: 60px;
}

content .choice .container_button {
  width: 267px;
  height: 272px;
  margin-bottom: 5%;
  display: flex;
  align-items: center;
}

content .message {
  font-family: "Roboto", serif;
  font-size: 24px;
  color: #cc0000;
  margin: 0;
  width: 100%;
}

content .guide {
  font-family: 'KdG';
  font-size: 42px;
  color: #1d1d1b;
  margin: 0;
  width: 100%;
}
